import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';
import {Product} from '../models/product';
import {environment} from '../../../environments/environment';
import {Compo} from '../models/compo';
import {PostResponse} from '../models/post-response';

@Injectable({
  providedIn: 'root'
})
export class CompoService {

  constructor(
    private http: HttpClient
  ) { }

  getCompos(): Observable<Compo[]> {
    return this.http.get<Compo[]>(`${environment.API_URL}/components`)
  }

  postCompo(compo: Compo): Observable<PostResponse> {
    return this.http.post<PostResponse>(`${environment.API_URL}/components`, compo)
  }
}
